<template>
  <div class="content" ref="recommendContent">
    <el-tabs v-model="activeTab" @tab-click="handleClick">
      <el-tab-pane label="" name="recommend">
        <div class="recommend-banner">
          <el-carousel :interval="4000" type="card" height="18rem">
            <el-carousel-item v-for="(item, i) of bannerList" :key="i">
              <img v-lazy="item.imageUrl" />
            </el-carousel-item>
          </el-carousel>
        </div>
        <!--  -->
        <div class="recommend-title"></div>
        <playlists-component
          :playLists="rePlayList"
          :playListShow="playListShow.recommend"
          idKey="id"
          playCountKey="playcount"
          imgKey="picUrl"
          nameKey="name"
        ></playlists-component>
      </el-tab-pane>
      <el-tab-pane label="" name="playlists">
        <recommend-playLists></recommend-playLists>
      </el-tab-pane>
      <el-tab-pane label="" name="broad"></el-tab-pane>
      <el-tab-pane label="" name="rank"></el-tab-pane>
      <el-tab-pane label="" name="singer"></el-tab-pane>
      <el-tab-pane label="" name="newsongs"></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import playlistsComponent from "../components/playilistsComponent";
import recommendPlayLists from "../components/recommend_playlists";
export default {
  data() {
    return {
      bannerList: [],
      rePlayList: [], //
      activeTab: "recommend", //tab
      playListShow: {
        //
        recommend: false, //
      },
    };
  },
  created() {
    this.getBanner(() => {
      this.getRecommendPlayList(() => {
        var t = setTimeout(() => {
          this.playListShow.recommend = true;
          clearTimeout(t);
        }, 0);
      });
    });
  },
  mounted() {},
  methods: {
    //
    getRecommendPlayList(success) {
      this.g
        .axios({
          url: this.g.host + "/recommend/resource",
        })
        .then((res) => {
          console.log("", res);
          this.rePlayList = res.data.recommend;
          if (success) {
            success();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //banner
    getBanner(success) {
      this.g
        .axios({
          url: this.g.host + "/banner",
        })
        .then((res) => {
          console.log("banner", res);
          this.bannerList = res.data.banners;
          if (success) {
            success();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //tab
    handleClick() {},
  },
  components: {
    playlistsComponent,
    recommendPlayLists,
  },
};
</script>

<style src='../css/views/recommend.css' scoped>
</style>