<template>
  <div class="clearfix" style="height: 100%">
    <div class="header clearfix" ref="header">
      <div class="header-logo"></div>
      <div class="header-showDrawerBtn icon-menu" @click="drawerShow"></div>
      <!-- ,  -->
      <div class="header-menu clearfix">
        <router-link
          :to="{ name: 'personal', query: { userId: userId } }"
          class="header-menu-my"
          ></router-link
        >
        <router-link to="/" class="header-menu-found"></router-link>
        <router-link to="" class="header-menu-countryside"></router-link>
        <router-link to="" class="header-menu-video"></router-link>
        <a class="header-menu-skin" @click="changeSkin($event)"></a>
        <router-link
          class="header-menu-search"
          :to="{ path: '/searchSuggestion' }"
        >
          <img src="../img/search.png" alt="" />
        </router-link>
      </div>
    </div>
    <!--  -->
    <div
      class="changeSkinMenu"
      :style="{ display: showSkinMenu ? 'flex' : 'none' }"
    >
      <el-switch v-model="darkModel" active-text=""> </el-switch>
    </div>
    <div
      class="leftDrawer"
      :class="{ showDrawer: showDrawer }"
      :style="{ height: drawerHeight + 'px' }"
      @mousedown="slideStart"
      @mouseup="slideEnd"
    >
      <audio :src="song" autoplay ref="audio"></audio>
      <div class="leftDrawer-title"><span></span></div>
      <router-link to="/">
        <div class="leftDrawer-btns">
          <img src="../img/music.png" /><span></span>
        </div>
      </router-link>
      <router-link :to="{ path: '/play', query: { fm: 1 } }">
        <div class="leftDrawer-btns">
          <img src="../img/FM.png" /><span>FM</span>
        </div>
      </router-link>
      <div class="leftDrawer-btns">
        <img src="../img/video.png" /><span></span>
      </div>
      <div class="leftDrawer-btns">
        <img src="../img/friends.png" /><span></span>
      </div>
      <router-link :to="{ path: '/playlistDetail' }">
        <div class="leftDrawer-btns">
          <img src="../img/dailyRecommend.png" /><span></span>
        </div>
      </router-link>
    </div>
    <div
      class="play-tuijian"
      :style="{ height: drawerHeight + 'px' }"
      ref="playTuijian"
    >
      <!-- :,-->
      <router-view
        ref="childRouter"
        :musicId="musicId"
        :contentHeight="contentHeight"
        :isPlay="isPlay"
        @songMsg="getplaylistSongMsg"
        @songList="getplaylistSongList"
        :fm="fm"
      ></router-view>
      <!--  -->
      <div class="play-setting clearfix" ref="playSetting">
        <router-link to="/play">
          <div
            class="play-setting-songImg"
            :style="{ backgroundImage: 'url(' + msg.pic + ')' }"
          ></div>
        </router-link>
        <div class="play-settings">
          <div class="play-settings-title">
            <span>{{
              msg.name
                ? msg.name + "&nbsp;&nbsp;" + msg.comment
                : "~"
            }}</span>
          </div>
          <div class="play-settings-sliderContent">
            <span class="play-settings-sliderContent-currentTime">{{
              currentTimeStr
            }}</span>
            <el-slider
              class="play-settings-sliderContent-slider"
              v-model="targetSlider"
              @change="sliderChange"
            ></el-slider>
            <span class="play-settings-sliderContent-endTime">{{
              durationStr
            }}</span>
          </div>
          <div class="play-settings-controll">
            <img src="../img/prev.png" @click="prev" />
            <img
              :src="
                isPlay
                  ? require('../img/pause.png')
                  : require('../img/play.png')
              "
              @click="playOrPause"
            />
            <img src="../img/next.png" @click="next" />
            <el-slider
              class="play-settings-controll-soundSlider"
              v-model="soundCurrent"
              @change="soundChange"
            ></el-slider>
            <img
              class="isLike"
              :src="
                isLike
                  ? require('../img/like.png')
                  : require('../img/unlike.png')
              "
              @click="liked"
            />
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="login-alert" :style="{ display: alertShow }">
      <div class="login-alert-title"></div>
      <div class="login-alert-form">
        <input
          type="text"
          placeholder="..."
          v-model="phone"
        />
        <input type="password" placeholder="..." v-model="pwd" />
      </div>
      <div class="login-alert-btn">
        <button @click="login"></button>
        <button></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phone: "",
      pwd: "",
      alertShow: "block",
      playShow: false, //
      playTitle: {}, //
      songList: [], //((play)fm)
      song: "",
      drawerHeight: 0, //
      comments: [],
      offset: 0,
      musicId: "", //id,
      nextSong: {}, //
      prevSong: {}, //
      msg: "", //
      targetSlider: 0, //slider()
      isPlay: false, //
      currentTimeStr: "", //(1:23)
      durationStr: "", //(1:23)
      soundCurrent: 0, //slider()
      fm: "", //fm,fm,fm,fm,playfm
      isLike: false, //
      contentHeight: 0, //()
      userId: 0,
      showSkinMenu: false, //
      darkModel: false, //
      showDrawer: false, //
      drawerSlideDistance: {
        //
        start: 0,
        end: 0,
      },
    };
  },
  created() {
    this.userId = window.localStorage.getItem("userId");
    this.g.cookie = window.localStorage.getItem("cookie");
    if (this.g.cookie && this.userId) {
      this.g.userId = this.userId;
      this.alertShow = "none";
      this.getLikedList();
      this.getCollectLists();
    }
  },
  mounted() {
    //audio
    var that = this;
    this.drawerHeight = window.innerHeight - this.$refs.header.offsetHeight;
    //,loading-(),=
    this.contentHeight =
      this.drawerHeight - this.$refs.playSetting.offsetHeight;
    this.$refs.playSetting.style.width =
      this.$refs.playTuijian.offsetWidth + "px";
    this.g.loadingAllPageHeight =
      this.drawerHeight - this.$refs.playTuijian.offsetHeight;
    window.onresize = () => {
      this.contentHeight =
        this.drawerHeight - this.$refs.playSetting.offsetHeight;
      //
      this.$refs.playSetting.style.width =
        this.$refs.playTuijian.offsetWidth + "px";
      this.g.loadingAllPageHeight =
        this.drawerHeight - this.$refs.playTuijian.offsetHeight;
    };
    var audio = this.$refs.audio;
    //
    this.soundCurrent = audio.volume * 100;
    audio.addEventListener("timeupdate", function () {
      //, 
      if (that.$route.name == "play") {
        that.$refs.childRouter.getPlayTime(audio.currentTime);
      }
      that.targetSlider = (audio.currentTime / audio.duration) * 100;
      that.currentTimeStr =
        Math.floor(audio.currentTime / 60) +
        ":" +
        (Math.floor(audio.currentTime % 60) >= 10
          ? Math.floor(audio.currentTime % 60)
          : "0" + Math.floor(audio.currentTime % 60));
      that.durationStr =
        Math.floor(audio.duration / 60) +
        ":" +
        (Math.floor(audio.duration % 60) >= 10
          ? Math.floor(audio.duration % 60)
          : "0" + Math.floor(audio.duration % 60));
      //,
      if (audio.paused) {
        that.isPlay = false;
      } else {
        that.isPlay = true;
      }
    });
    audio.onended = function () {
      //
      that.getSongUrl(
        that.nextSong.item.id,
        that.nextSong.item.ar,
        that.nextSong.item.al.name,
        that.nextSong.index
      );
    };
  },
  methods: {
    //
    login() {
      this.g
        .axios({
          method: "GET",
          url: this.g.host + "/login/cellphone",
          params: {
            phone: this.phone,
            password: this.pwd,
          },
        })
        .then((res) => {
          console.log(res);
          if (res.status == 200) {
            this.alertShow = "none";
            //cookie(cookie,)
            var cookie = res.data.cookie;
            // cookie = cookie.split(" Path=/;");
            // cookie.forEach((item, i) => {
            //   if (item) {
            //     document.cookie = item + " Path=/;";
            //   }
            // });

            //document.cookie = "domain=" + document.domain;
            this.g.cookie = res.data.cookie;
            window.localStorage.setItem("cookie", cookie);
            this.g.userId = res.data.profile.userId;
            this.userId = this.g.userId;
            console.log(this.g.userId);
            window.localStorage.setItem("userId", this.g.userId);
            this.getLikedList();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    //url,similarMsg, comment,name,alias()
    getSongUrl(id, singer, zhuanji, index, similarMsg) {
      console.log(id);
      //id
      var pic = "",
        name = "",
        comment = "";
      if (similarMsg) {
        name = similarMsg.name;
        pic = similarMsg.pic;
      } else {
        for (var item of this.songList) {
          if (item.id == id) {
            //,,url,
            name = item.name;
            comment = item.alia.join(); //
            pic = item.al.picUrl; //,
          }
        }
      }
      this.g
        .axios({
          url: this.g.host + "/song/url",
          params: {
            id: id,
          },
        })
        .then((res) => {
          console.log("url", res);
          this.musicId = id;
          this.song = res.data.data[0].url;
          this.msg = {
            name: name,
            comment: comment,
            pic: pic,
            musicUrl: res.data.data[0].url,
            br: res.data.data[0].br, // , 
            singer: singer, //
            zhuanji: zhuanji, //
            size: res.data.data[0].size, //
          };
          this.g.getMusic(id, this.msg);
          //url,
          for (var i = 0; i <= this.g.likedMusic.length - 1; i++) {
            if (this.g.likedMusic[i] == id) {
              this.isLike = true;
              break;
            }
            if (i == this.g.likedMusic.length - 1) {
              this.isLike = false;
            }
          }
          // 
          if (this.$route.name == "play") {
            this.$refs.childRouter.refresh();
          }

          this.offset = 0;
          this.comments = [];

          //,,,,
          console.log("", this.songList[index + 1]);
          if (!this.songList[index + 1]) {
            this.nextSong = {
              index: 0,
              item: this.songList[0],
            };
          } else {
            this.nextSong = {
              index: index + 1,
              item: this.songList[index + 1],
            };
          }
          //,, ,index-1
          if (!this.songList[index - 1]) {
            this.prevSong = {
              index: -1,
              item: {},
            };
          } else {
            this.prevSong = {
              index: index - 1,
              item: this.songList[index - 1],
            };
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    ///
    liked() {
      this.isLike = this.isLike ? false : true;
      this.g
        .axios({
          url: this.g.host + "/like",
          params: {
            id: this.g.music.id, //id
            like: this.isLike, //,,
          },
        })
        .then((res) => {
          console.log("/", res);
          if (res.data.code == 200) {
            ///()
            for (var i = 0; i <= this.songList.length - 1; i++) {
              if (this.songList[i].id == this.g.music.id) {
                this.songList[i].liked = this.isLike;
                console.log(this.songList[i].liked);
                break;
              }
            }
            //likedMusic, 
            if (this.isLike) {
              //
              this.g.likedMusic.push(this.g.music.id);
            } else {
              //
              this.g.likedMusic = this.g.likedMusic.filter((item, i) => {
                return item != this.g.music.id;
              });
            }
            console.log(this.g.likedMusic);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    cancel() {
      this.playShow = false;
    },
    ///
    playOrPause() {
      if (this.isPlay) {
        this.$refs.audio.pause();
      } else {
        this.$refs.audio.play();
      }
    },
    //
    prev() {
      if (this.prevSong.index != -1) {
        this.getSongUrl(
          this.prevSong.item.id,
          this.prevSong.item.ar,
          this.prevSong.item.al.name,
          this.prevSong.index
        );
      } else {
        alert("");
      }
    },
    //
    next() {
      if (this.nextSong.index != 0) {
        this.getSongUrl(
          this.nextSong.item.id,
          this.nextSong.item.ar,
          this.nextSong.item.al.name,
          this.nextSong.index
        );
      } else {
        //fmfm
        if (this.fm) {
          this.fm++;
        } else {
          alert("");
        }
      }
    },
    //()change,this.targetSlider()
    sliderChange(res) {
      this.$refs.audio.currentTime = (res / 100) * this.$refs.audio.duration;
    },
    //()change
    soundChange(res) {
      this.$refs.audio.volume = res / 100;
    },
    //
    getplaylistSongList(songList, fm) {
      this.songList = songList;
      this.fm = fm;
      if (this.fm) {
        this.getSongUrl(
          this.songList[0].id,
          this.songList[0].ar,
          this.songList[0].al.name,
          0
        );
      }
    },
    //url
    getplaylistSongMsg(id, singer, zhuanji, index, similarMsg) {
      this.getSongUrl(id, singer, zhuanji, index, similarMsg);
    },
    //
    getLikedList() {
      this.g
        .axios({
          url: this.g.host + "/likelist",
          params: {
            uid: this.userId,
          },
        })
        .then((res) => {
          console.log("id", res);
          this.g.getLikedMusic(res.data.ids);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    changeSkin(e) {
      e.stopPropagation();
      if (this.showSkinMenu) {
        this.showSkinMenu = false;
      } else {
        this.showSkinMenu = true;
      }
    },
    //
    drawerShow() {
      if (this.showDrawer) {
        this.showDrawer = false;
      } else {
        this.showDrawer = true;
      }
    },
    // ,x
    slideStart(e) {
      e.stopPropagation();
      this.drawerSlideDistance.start = e.clientX;
    },
    slideEnd(e) {
      e.stopPropagation();
      this.drawerSlideDistance.end = e.clientX;
      if (this.drawerSlideDistance.start - this.drawerSlideDistance.end > 80) {
        this.showDrawer = false;
      }
    },
    //  :id
    getCollectLists() {
      this.g
        .axios({
          url: this.g.host + "/user/playlist",
          params: {
            uid: this.userId,
            limit: 10000,
          },
          // header:{
          //   "Content-Type":"application/x-www-form-urlencoded"
          // }
        })
        .then((res) => {
          console.log("", res);
          this.g.userPlayListIds = [];
          for (let item of res.data.playlist) {
            this.g.userPlayListIds.push(item.id);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  watch: {
    darkModel() {
      this.$emit("getDarkModel", this.darkModel);
    },
  },
};
</script>

<style  scoped src='../css/views/index.css'></style>